.Trigger {
  cursor: pointer;
  font-size: 1.5em;
  height: 8.5em;
  right: 0;
  top: 3em;
  position: absolute;
  width: 3.7em;
  -webkit-transform: translateX(100%);
      -ms-transform: translateX(100%);
          transform: translateX(100%);
}

.Trigger div {
  position: absolute;
  top: 0;
}

.Trigger .ring1 {
  background: #282828;
  background: -webkit-gradient(linear, left top, left bottom, from(#282828), color-stop(14%, #959595), color-stop(37%, #d1d1d1), color-stop(49%, #bababa), color-stop(67%, #959595), to(#212121));
  background: -webkit-linear-gradient(top, #282828 0%, #959595 14%, #d1d1d1 37%, #bababa 49%, #959595 67%, #212121 100%);
  background: -o-linear-gradient(top, #282828 0%, #959595 14%, #d1d1d1 37%, #bababa 49%, #959595 67%, #212121 100%);
  background: linear-gradient(to bottom, #282828 0%, #959595 14%, #d1d1d1 37%, #bababa 49%, #959595 67%, #212121 100%);
  border-radius: 0 0.2em 0.2em 0;
  -webkit-box-shadow: inset 0 0.2em 0.3em rgba(0, 0, 0, 0.8);
          box-shadow: inset 0 0.2em 0.3em rgba(0, 0, 0, 0.8);
  height: 100%;
  left: 0;
  width: 0.8em;
  z-index: 2;
}

.Trigger .ring1 .shadow {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(149, 149, 149, 0.2)), color-stop(46%, rgba(13, 13, 13, 0.18)), color-stop(50%, rgba(1, 1, 1, 0.18)), color-stop(53%, rgba(10, 10, 10, 0.18)), color-stop(76%, rgba(78, 78, 78, 0.17)), color-stop(87%, rgba(56, 56, 56, 0.16)), to(rgba(27, 27, 27, 0.15)));
  background: -webkit-linear-gradient(top, rgba(149, 149, 149, 0.2) 0%, rgba(13, 13, 13, 0.18) 46%, rgba(1, 1, 1, 0.18) 50%, rgba(10, 10, 10, 0.18) 53%, rgba(78, 78, 78, 0.17) 76%, rgba(56, 56, 56, 0.16) 87%, rgba(27, 27, 27, 0.15) 100%);
  background: -o-linear-gradient(top, rgba(149, 149, 149, 0.2) 0%, rgba(13, 13, 13, 0.18) 46%, rgba(1, 1, 1, 0.18) 50%, rgba(10, 10, 10, 0.18) 53%, rgba(78, 78, 78, 0.17) 76%, rgba(56, 56, 56, 0.16) 87%, rgba(27, 27, 27, 0.15) 100%);
  background: linear-gradient(to bottom, rgba(149, 149, 149, 0.2) 0%, rgba(13, 13, 13, 0.18) 46%, rgba(1, 1, 1, 0.18) 50%, rgba(10, 10, 10, 0.18) 53%, rgba(78, 78, 78, 0.17) 76%, rgba(56, 56, 56, 0.16) 87%, rgba(27, 27, 27, 0.15) 100%);
  height: 50%;
  left: 0;
  opacity: 0;
  position: absolute;
  top: 0;
  width: 0.1em;
  z-index: 1;
  -webkit-transition: .6s;
  -o-transition: .6s;
  transition: .6s;
}

.Trigger .ring2 {
  background: #282828;
  background: -webkit-gradient(linear, left top, left bottom, from(#282828), color-stop(14%, #959595), color-stop(37%, #d1d1d1), color-stop(49%, #bababa), color-stop(67%, #959595), to(#212121));
  background: -webkit-linear-gradient(top, #282828 0%, #959595 14%, #d1d1d1 37%, #bababa 49%, #959595 67%, #212121 100%);
  background: -o-linear-gradient(top, #282828 0%, #959595 14%, #d1d1d1 37%, #bababa 49%, #959595 67%, #212121 100%);
  background: linear-gradient(to bottom, #282828 0%, #959595 14%, #d1d1d1 37%, #bababa 49%, #959595 67%, #212121 100%);
  border-radius: 0 0.2em 0.2em 0;
  -webkit-box-shadow: inset 0 0.2em 0.3em rgba(0, 0, 0, 0.8);
          box-shadow: inset 0 0.2em 0.3em rgba(0, 0, 0, 0.8);
  height: 80%;
  left: 0.8em;
  overflow: hidden;
  top: 10%;
  width: 1em;
  z-index: 1;
}

.Trigger .ring2 .shadow {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(149, 149, 149, 0.15)), color-stop(46%, rgba(13, 13, 13, 0.15)), color-stop(50%, rgba(1, 1, 1, 0.15)), color-stop(53%, rgba(10, 10, 10, 0.15)), color-stop(76%, rgba(78, 78, 78, 0.15)), color-stop(87%, rgba(56, 56, 56, 0.15)), to(rgba(27, 27, 27, 0.15)));
  background: -webkit-linear-gradient(top, rgba(149, 149, 149, 0.15) 0%, rgba(13, 13, 13, 0.15) 46%, rgba(1, 1, 1, 0.15) 50%, rgba(10, 10, 10, 0.15) 53%, rgba(78, 78, 78, 0.15) 76%, rgba(56, 56, 56, 0.15) 87%, rgba(27, 27, 27, 0.15) 100%);
  background: -o-linear-gradient(top, rgba(149, 149, 149, 0.15) 0%, rgba(13, 13, 13, 0.15) 46%, rgba(1, 1, 1, 0.15) 50%, rgba(10, 10, 10, 0.15) 53%, rgba(78, 78, 78, 0.15) 76%, rgba(56, 56, 56, 0.15) 87%, rgba(27, 27, 27, 0.15) 100%);
  background: linear-gradient(to bottom, rgba(149, 149, 149, 0.15) 0%, rgba(13, 13, 13, 0.15) 46%, rgba(1, 1, 1, 0.15) 50%, rgba(10, 10, 10, 0.15) 53%, rgba(78, 78, 78, 0.15) 76%, rgba(56, 56, 56, 0.15) 87%, rgba(27, 27, 27, 0.15) 100%);
  border-radius: 0 0.2em 0.2em 0;
  height: 50%;
  left: 0;
  opacity: 0;
  position: absolute;
  top: 0;
  width: 0.1em;
  z-index: 1;
  -webkit-transition: .6s;
  -o-transition: .6s;
  transition: .6s;
}

.Trigger .arm {
  background: #0d0d0d;
  background: -webkit-gradient(linear, left top, right top, from(#0d0d0d), color-stop(47%, #4e4e4e), color-stop(87%, #383838), to(#1b1b1b));
  background: -webkit-linear-gradient(left, #0d0d0d 0%, #4e4e4e 47%, #383838 87%, #1b1b1b 100%);
  background: -o-linear-gradient(left, #0d0d0d 0%, #4e4e4e 47%, #383838 87%, #1b1b1b 100%);
  background: linear-gradient(to right, #0d0d0d 0%, #4e4e4e 47%, #383838 87%, #1b1b1b 100%);
  border-radius: 0 0 0.4em 0.4em;
  -webkit-box-shadow: inset 0 0.2em 0.5em rgba(0, 0, 0, 0.8);
          box-shadow: inset 0 0.2em 0.5em rgba(0, 0, 0, 0.8);
  height: 50%;
  left: 1em;
  top: -2.5em;
  width: 0.6em;
  z-index: 3;
  overflow: visible !important;
  -webkit-transition: .6s;
  -o-transition: .6s;
  transition: .6s;
}

.Trigger .arm .knob {
  background: #ff6363;
  background: -webkit-radial-gradient(center ellipse, #ff6363 0%, #cf0404 100%);
  background: -o-radial-gradient(center ellipse, #ff6363 0%, #cf0404 100%);
  background: radial-gradient(ellipse at center, #ff6363 0%, #cf0404 100%);
  border-radius: 1em;
  -webkit-box-shadow: 0 0.1em 0.1em rgba(0, 0, 0, 0.4);
          box-shadow: 0 0.1em 0.1em rgba(0, 0, 0, 0.4);
  height: 1.6em;
  left: -0.7em;
  top: -1.5em;
  width: 2em;
  z-index: 4;
  -webkit-transition: .6s;
  -o-transition: .6s;
  transition: .6s;
}

.Trigger .arm-shadow {
  background: #000;
  border-radius: 1em;
  bottom: 6.6em;
  -webkit-box-shadow: 0 0.1em 0.1em rgba(0, 0, 0, 0.4);
          box-shadow: 0 0.1em 0.1em rgba(0, 0, 0, 0.4);
  height: 0.6em;
  left: 0.9em;
  top: 1.4em;
  width: .8em;
  z-index: 2;
  -webkit-transition: .6s;
  -o-transition: .6s;
  transition: .6s;
}

.Machine--going .Trigger .arm {
  top: 4.5em;
  height: 2%;
}

.Machine--going .Trigger .arm .knob {
  top: -2em;
  height: 2em;
}

.Machine--going .Trigger .arm-shadow {
  top: 4em;
}

.Machine--going .Trigger .ring1 .shadow,
.Machine--going .Trigger .ring2 .shadow {
  top: 50%;
  opacity: 1;
}


